.fixed__div {
  background-color: #141414;
  color: white;
  width: 5vw;
  right: 0;
  height: 100vh;
  border-left: 1px solid white;
  z-index: 99999;
  position: fixed;
}

.fixed__div .fixed__div__menu {
  top: 2rem;
  margin: auto;
  text-align: center;
  position: fixed;
  right: 1.6vw;
}

.fixed__div .fixed__div__menu input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  -webkit-touch-callout: none;
}

.fixed__div .fixed__div__menu span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #FFC03F;
  border-radius: 3px;
  z-index: 1;
  -webkit-transform-origin: 4px 0px;
          transform-origin: 4px 0px;
  transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),opacity 0.55s ease,-webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),opacity 0.55s ease;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),opacity 0.55s ease,-webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

.fixed__div .fixed__div__menu span:first-child {
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
}

.fixed__div .fixed__div__menu span:nth-last-child(2) {
  -webkit-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
}

.fixed__div .arrow {
  color: #FFC03F;
  bottom: 3rem;
  font-size: 4rem;
  width: 5vw;
  text-align: center;
  cursor: pointer;
  position: fixed;
  text-align: center;
}

